@import '../../sass/abstracts/mixins';

.about {
  flex-direction: column;
  margin-top: 3em;
  backface-visibility: hidden; // to avoid the flicker on the animations below

  .moveLeft {
    @include moveOnLoadAnimation(moveInLeft, 1s);
    // animations coming from sass/abstracts/mixins
  }

  &__name {
    color: --clr-primary;
  }
  &__role {
    margin-top: 1.2em;
    @include moveOnLoadAnimation(moveInRight, 900ms);
  }

  &__desc {
    font-size: 1rem;
    max-width: 40rem;
    @include moveOnLoadAnimation(bottonUp, 900ms);
  }
}

.about__desc,
.about__contact {
  margin-top: 2.4em;
}

.about .link--icon {
  margin-right: 0.8em;
}

.about .btn--outline {
  margin-right: 1em;
}

@media (max-width: 60rem) {
  .app .about {
    align-items: flex-start;
    margin-top: 2em;
  }
}

/* this keyFrames are just basically css */

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
    /* left to right */
  }
  70% {
    transform: translateX(1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(10rem);
    /*right to left */
  }
  80% {
    transform: translateX(-1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes bottonUp {
  0% {
    opacity: 0;
    transform: translateY(5rem);
    /*right to left */
  }
  80% {
    transform: translateY(-1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
/* ---------------------------------------------------------------------------- */
